/* Ensure consistent layout behavior across the app */
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Center the dashboard and make it responsive */
.dashboard-container {
  width: 100%;
  max-width: 1200px; /* Limit width for large screens */
  margin: 0 auto; /* Center the container */
  padding: 16px; /* Add padding for smaller screens */
}

/* Flexbox layout for charts */
.chart-container {
  display: flex;
  gap: 12px; /* Space between labels and chart */
  align-items: flex-start; /* Align labels and bars to the top */
  width: 100%; /* Ensure the chart takes full width */
}

/* Fixed width for the labels section */
.labels-container {
  width: 150px; /* Adjust width as needed */
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Spread labels evenly */
}

/* Individual label styling to align with bars */
.label-item {
  height: 55px; /* Match the bar height */
  display: flex;
  align-items: center;
  justify-content: flex-end; /* Align text to the right */
  white-space: nowrap; /* Prevent text wrapping */
}

/* The chart takes remaining space */
.bar-chart-wrapper {
  flex: 1; /* Expand to fill remaining space */
  min-width: 0; /* Prevent overflow issues */
}

/* Tooltip wrapper fix to ensure proper interaction */
.recharts-tooltip-wrapper {
  pointer-events: auto; /* Ensure tooltips work correctly */
}

/* Styling for tooltip content */
.tooltip {
  display: block;
  position: absolute;
  z-index: 1000;
  pointer-events: none;
}

/* Prevent issues with textareas resizing */
textarea {
  resize: vertical;
}

/* Reset list elements */
ol, ul, menu {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Ensure tables don't have gaps between borders */
table {
  border-collapse: collapse;
  border-color: inherit;
  text-indent: 0;
}

/* Remove margins from certain block elements */
blockquote, dl, dd, h1, h2, h3, h4, h5, h6, hr, figure, p, pre {
  margin: 0;
}

/* Dialog reset */
dialog {
  padding: 0;
}

/* Buttons and interactive elements get pointer cursor */
button, [role="button"] {
  cursor: pointer;
}

/* Ensure hidden elements remain hidden */
[hidden] {
  display: none;
}

/* Responsive media elements */
img, svg, video, canvas, audio, iframe, embed, object {
  display: block;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

/* Tailwind overrides: Ensure labels align correctly */
.labels-container {
  align-items: flex-end; /* Align labels to the right */
  padding-right: 12px; /* Add spacing between labels and bars */
}

/* Adjust margins for charts to fix layout */
.chart-container .bar-chart-wrapper {
  margin-left: 0;
  margin-right: 10px;
}

/* Optional spacing adjustments for mobile */
@media (max-width: 768px) {
  .dashboard-container {
    padding: 8px; /* Reduce padding on smaller screens */
  }

  .labels-container {
    width: 100px; /* Adjust label width for small screens */
  }
}
